"use client";

import Image from "next/image";
import { useEffect, useMemo, useState } from "react";

import { Container } from "@/components/Container";

type Step = {
  title: string;
  description: string;
  overlayLabel: string;
};

export function ScrollStory() {
  const steps: Step[] = useMemo(
    () => [
      {
        title: "Свет и приватность",
        description:
          "Настраиваем: мягкий дневной свет или полное затемнение. Вы получаете красивый интерьер и комфорт.",
        overlayLabel: "Blackout / полупрозрачные",
      },
      {
        title: "Фактура ткани",
        description:
          "Тактильность и складка решают ощущение премиальности. Подбираем материал под интерьер и сценарий комнаты.",
        overlayLabel: "Фактура / плотность",
      },
      {
        title: "Декор и детали",
        description:
          "Карнизы, подхваты, фурнитура и аксессуары — именно они делают проект законченно дорогим.",
        overlayLabel: "Декор / фурнитура",
      },
    ],
    [],
  );

  const [active, setActive] = useState(0);

  useEffect(() => {
    const els = Array.from(document.querySelectorAll("[data-kr-step]"));
    if (els.length === 0) return;

    const io = new IntersectionObserver(
      (entries) => {
        const visible = entries
          .filter((e) => e.isIntersecting)
          .sort((a, b) => (b.intersectionRatio ?? 0) - (a.intersectionRatio ?? 0));
        if (visible[0]) {
          const idx = Number((visible[0].target as HTMLElement).dataset.krStep);
          if (!Number.isNaN(idx)) setActive(idx);
        }
      },
      {
        root: null,
        threshold: [0.2, 0.35, 0.5, 0.65],
        rootMargin: "-20% 0px -55% 0px",
      },
    );

    els.forEach((el) => io.observe(el));
    return () => io.disconnect();
  }, []);

  return (
    <section className="py-10 sm:py-14">
      <Container>
        <div className="grid gap-10 lg:grid-cols-12">
          <div className="lg:col-span-6">
            <div className="sticky top-24">
              <div className="relative overflow-hidden rounded-[28px] border border-black/5 bg-white/50 shadow-sm backdrop-blur dark:border-white/10 dark:bg-white/5">
                <div className="absolute inset-0 bg-[radial-gradient(700px_circle_at_15%_10%,var(--glow),transparent_60%)]" />
                <div
                  className="absolute inset-0 opacity-0 transition-opacity duration-500"
                  style={{ opacity: active === 0 ? 1 : 0 }}
                />
                <div
                  className="absolute inset-0 opacity-0 transition-opacity duration-500"
                  style={{ opacity: active === 1 ? 1 : 0 }}
                />
                <div
                  className="absolute inset-0 opacity-0 transition-opacity duration-500"
                  style={{ opacity: active === 2 ? 1 : 0 }}
                />

                <div className="relative aspect-[4/3]">
                  <Image
                    src="/catalog/curtains.jpg"
                    alt="Шторы"
                    fill
                    sizes="(min-width: 1024px) 50vw, 100vw"
                    className="object-cover"
                    priority
                  />
                  <div className="absolute inset-0 bg-[linear-gradient(to_top,rgba(0,0,0,0.55),transparent_65%)]" />

                  <div className="absolute left-5 top-5 rounded-full border border-white/20 bg-black/35 px-3 py-1 text-xs font-semibold tracking-wide text-white/90 backdrop-blur">
                    {steps[active]?.overlayLabel}
                  </div>

                  <div className="absolute bottom-0 left-0 right-0 p-6">
                    <div className="text-2xl font-semibold tracking-tight text-white">
                      {steps[active]?.title}
                    </div>
                    <div className="mt-2 max-w-md text-sm leading-6 text-white/80">
                      {steps[active]?.description}
                    </div>
                  </div>

                  <svg
                    className="absolute inset-0 h-full w-full"
                    viewBox="0 0 100 100"
                    preserveAspectRatio="none"
                    aria-hidden="true"
                  >
                    <path
                      d="M18,78 C28,60 36,52 50,50 C64,48 74,58 86,78"
                      fill="none"
                      stroke="rgba(255,255,255,0.65)"
                      strokeWidth="0.9"
                      strokeLinecap="round"
                      strokeDasharray="2 2"
                      style={{
                        opacity: active === 1 ? 1 : 0.15,
                        transition: "opacity 500ms ease",
                      }}
                    />
                    <circle
                      cx="50"
                      cy="50"
                      r="1.9"
                      fill="rgba(255,255,255,0.85)"
                      style={{
                        opacity: active === 1 ? 1 : 0.15,
                        transition: "opacity 500ms ease",
                      }}
                    />
                  </svg>
                </div>
              </div>

              <div className="mt-4 text-xs text-[color:var(--muted)]">
                Скролль вниз — подсветка меняется, как в продуктовых сторителлингах.
              </div>
            </div>
          </div>

          <div className="lg:col-span-6">
            <div className="text-xs font-semibold tracking-[0.32em] text-[color:var(--muted)]">
              ПРОЦЕСС
            </div>
            <h2 className="mt-4 text-3xl font-semibold tracking-tight text-[color:var(--fg)] sm:text-4xl">
              От выбора до идеальной складки
            </h2>
            <p className="mt-3 max-w-xl text-sm leading-6 text-[color:var(--muted)] sm:text-base">
              Быстро, спокойно и профессионально. Без длинных объяснений — только
              важное.
            </p>

            <div className="mt-8 grid gap-6">
              {steps.map((s, idx) => (
                <div
                  key={s.title}
                  data-kr-step={idx}
                  className={`rounded-3xl border p-6 shadow-sm transition ${
                    active === idx
                      ? "border-[color:var(--accent)]/35 bg-[color:var(--accent-soft)]"
                      : "border-black/5 bg-white/40 dark:border-white/10 dark:bg-white/5"
                  }`}
                >
                  <div className="text-sm font-semibold text-[color:var(--fg)]">
                    {String(idx + 1).padStart(2, "0")}
                  </div>
                  <div className="mt-3 text-lg font-semibold text-[color:var(--fg)]">
                    {s.title}
                  </div>
                  <div className="mt-2 text-sm leading-6 text-[color:var(--muted)]">
                    {s.description}
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
      </Container>
    </section>
  );
}
